html {
  box-sizing: border-box;
  height: 100%;
  hyphens: auto;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100%;  // –ï—Å–ª–∏ –≤—ã—Å–æ—Ç–∞ –∑–∞–¥–∞–Ω–∞ –≤ –ø—Ä–æ—Ü–µ–Ω—Ç–∞—Ö, —Ç–æ –ø—Ä–æ—Ü–µ–Ω—Ç—ã –±–µ—Ä—É—Ç—Å—è –æ—Ç –≤—ã—Å–æ—Ç—ã —Ä–æ–¥–∏—Ç–µ–ª—å—Å–∫–æ–≥–æ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞(html => body => .wrapper). –ï—Å–ª–∏ —è–≤–Ω–æ –Ω–µ –∑–∞–¥–∞–≤–∞—Ç—å –≤—ã—Å–æ—Ç—É —Ä–æ–¥–∏—Ç–µ–ª—è–º .wrapper –±—Ä–∞—É–∑–µ—Ä —É—Å—Ç–∞–Ω–æ–≤–∏—Ç –≤—ã—Å–æ—Ç—É —ç—Ç–æ–≥–æ –±–ª–æ–∫–∞ –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –≤—ã—Å–æ—Ç—ã –µ–≥–æ —Å–æ–¥–µ—Ä–∂–∏–º–æ–≥–æ. —ç—Ç–æ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –¥–ª—è –ø—Ä–∏–∂–∏–º–∞–Ω–∏—è —Ñ—É—Ç–µ—Ä–∞ –∫ –Ω–∏–∑—É —Å—Ç—Ä–∞–Ω–∏—Ü—ã –≤ —Ç–µ—Ö —Å–ª—É—á–∞—è—Ö, –∫–æ–≥–¥–∞ –≤—ã—Å–æ—Ç–∞ –∫–æ–Ω—Ç–µ–Ω—Ç–∞ –Ω–µ –ø–æ–∑–≤–æ–ª—è–µ—Ç —ç—Ç–æ–≥–æ —Å–¥–µ–ª–∞—Ç—å//
  font-size: 21px;
  line-height: 34px;
  font-weight: 400;
  background-color: $color-text-1;
  // overflow: hidden; //–Ω–µ–æ–±—Ö–æ–¥–∏–º, —á—Ç–æ–±—ã –Ω–∏—á–µ–≥–æ –Ω–µ –≤—ã–ø–∞–¥–∞–ª–æ –∑–∞ –ø—Ä–µ–¥–µ–ª—ã —Å—Ç—Ä–∞–Ω–∏—Ü—ã//

  @media (max-width: 767px) {
    font-size: 20px;
    line-height: 23px;
  }
}

a {
  text-decoration: none;
}

li {
  list-style-type: none;
}

ul {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
p,
blockquote {
  margin: 0;
}

button {
  padding: 0;
  border: none;
  background-color: transparent;
}

address {
  font-style: normal;
}

fieldset {
  padding: 0;
  margin: 0;
  border: 0;
}

.wrapper {
  max-width: 1330px;  //—à–∏—Ä–∏–Ω–∞ –º–∞–∫–µ—Ç–∞//
  padding-right: 15px;
  padding-left: 15px;
  margin: 0 auto;  //—Ü–µ–Ω—Ç—Ä–∏—Ä—É–µ–º –º–∞–∫–µ—Ç//
  position: relative;
}

div {
  img {
    display: block;  //–∫–∞—Ä—Ç–∏–Ω–∫–∞ —Å—Ç–∞–Ω–æ–≤–∏—Ç—Å—è –±–ª–æ—á–Ω—ã–º —ç–ª–µ–º–µ–Ω—Ç–æ–º//
  }
}
